import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/Home.vue";

const routes = [
  {
    path: "/",
    name: "home",
    component: Home,
  },
  {
    path: "/matricula",
    name: "matricula",
    component: () => {
      return import("../views/ConfigMatricula.vue");
    },
  },
  {
    path: "/injetora",
    name: "injetora",
    component: () => {
      return import("../views/ConfigInjetora.vue");
    },
  },
  {
    path: "/validando",
    name: "validando",
    component: () => {
      return import("../views/ValidaRequest.vue");
    },
  },
  {
    path: "/dashboard",
    name: "dashboard",
    component: () => {
      return import("../views/ShowDashboard.vue");
    },
    children: [
      {
        path: "",
        name: "main",
        component: () => {
          return import("../components/dashboard/MainView.vue");
        },
      },
      {
        path: "parada",
        name: "parada",
        component: () => {
          return import("../components/dashboard/ApontamentoParada.vue");
        },
      },
      {
        path: "refugo",
        name: "refugo",
        component: () => {
          return import("../components/dashboard/ApontamentoRefugo.vue");
        },
      },
      {
        path: "cards-parada",
        name: "cards-parada",
        component: () => {
          return import("../components/dashboard/CardsParada.vue");
        },
      },
      {
        path: "consulta-refugo",
        name: "cosulta-refugo",
        component: () => {
          return import("../components/dashboard/ConsultaRefugo.vue");
        },
      },

    ],
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
